  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 1rem;
  box-sizing: border-box;
}

input.input-medieval {
  flex: 1;
  margin-bottom: 0.5rem;
}

.button-medieval {
  width: 70%;
  margin: 1rem auto 0 auto;
  display: block;
}

.login-error {
  color: #992e2e;
  font-weight: bold;
  margin-bottom: 0.5rem;
  text-align: center;
  font-family: "MedievalSharp", serif;
  font-size: 0.95rem;
}

.input-icon-group {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  margin-bottom: 0.5rem;
}

.login-links {
  display: flex;
  justify-content: center;
  gap: 0.5rem;
  margin-top: 1.2rem;
  font-family: "MedievalSharp", serif;
}

.medieval-link {
  color: #7b5e3b;
  text-decoration: underline;
  cursor: pointer;
  font-weight: bold;
  transition: color 0.2s;
  background: none;
  border: none;
  padding: 0;
  font-size: inherit;
}

.medieval-link:hover {
  color: #a88d5a;
}

.medieval-disclaimer {
  background: linear-gradient(#fdf8e4, #f4e6b3);
  border: 2px solid #a67c52;
  border-radius: 8px;
  padding: 1.2rem 1.5rem;
  margin-bottom: 1.2rem;
  font-family: "Georgia", serif;
  font-size: 1.08rem;
  color: #3b2a1a;
  box-shadow: inset 0 0 8px rgba(139, 69, 19, 0.15), 2px 2px 6px rgba(0, 0, 0, 0.1);
  text-align: left;
}
.medieval-disclaimer p {
  margin-bottom: 0.5rem;
  line-height: 1.5;
}
.medieval-disclaimer p:last-child {
  margin-bottom: 0;
}
.medieval-disclaimer strong {
  color: #5b3e1d;
}

.terms-section {
  margin: 1.2rem 0 0.5rem 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
}
.terms-section .terms-checkbox {
  display: flex;
  align-items: flex-start;
  gap: 0.3em;
  flex-wrap: wrap;
  justify-content: center;
}
.terms-section .terms-checkbox input[type=checkbox] {
  width: 20px;
  height: 20px;
  accent-color: #8b5e3c;
  margin: 0 8px 0 0;
  flex-shrink: 0;
  margin-top: 0;
  position: relative;
  top: 1px;
}
.terms-section .terms-checkbox p {
  margin: 0;
  font-size: 1rem;
  line-height: 1.4;
  color: #3b2a1a;
  font-family: "Georgia", serif;
  cursor: pointer;
  display: flex;
  align-items: flex-start;
  gap: 0.3em;
  flex-wrap: wrap;
  text-align: center;
  justify-content: center;
}
.terms-section .terms-checkbox .medieval-link {
  display: inline;
  margin-left: 0.3em;
  white-space: normal;
  word-break: break-word;
}

.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;
  padding: 1rem;
  box-sizing: border-box;
}

.modal-conteudo {
  background: linear-gradient(#fef8e7, #f5e4bc);
  border: 2px solid #8b5e3c;
  border-radius: 10px;
  max-width: 95vw;
  max-height: 95vh;
  width: 100%;
  overflow-y: auto;
  padding: 20px;
  box-sizing: border-box;
  font-family: "MedievalSharp", cursive;
  position: relative;
}

.modal-titulo {
  font-size: 1.5rem;
  margin-bottom: 1rem;
  text-align: center;
  color: #5b3e1d;
}

.modal-opcoes {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  margin-bottom: 1.5rem;
}
.modal-opcoes h4 {
  color: #5b3e1d;
  margin: 1rem 0 0.5rem 0;
  font-size: 1.1rem;
}
.modal-opcoes p {
  color: #3b2a1a;
  line-height: 1.5;
  margin: 0.5rem 0;
  font-size: 0.9rem;
}
.modal-opcoes ul {
  margin: 0.5rem 0;
  padding-left: 1.5rem;
}
.modal-opcoes ul li {
  color: #3b2a1a;
  line-height: 1.4;
  margin-bottom: 0.5rem;
  font-size: 0.9rem;
}
.modal-opcoes ul li strong {
  color: #5b3e1d;
}
.modal-opcoes .contact-info {
  font-style: italic;
  color: #8b5e3c;
  text-align: center;
  margin-top: 1rem;
  padding: 0.8rem;
  background: rgba(139, 94, 60, 0.1);
  border-radius: 6px;
}

.modal-botoes {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-top: 1em;
}
.modal-botoes .button-medieval {
  width: auto;
  margin: 0;
}

@media (max-width: 768px) {
  .consulta-layout {
    padding: 0.5rem;
    justify-content: flex-start;
    min-height: auto;
  }
  .parchment-card {
    max-width: 100%;
    padding: 1rem;
    margin: 1rem 0;
  }
  .medieval-disclaimer {
    font-size: 0.95rem;
    padding: 1rem 0.8rem;
  }
  .terms-section .terms-checkbox input[type=checkbox] {
    width: 22px;
    height: 22px;
  }
  .terms-section .terms-checkbox p {
    font-size: 0.95rem;
    text-align: center;
  }
  .modal-conteudo {
    font-size: 0.9rem;
    padding: 16px;
    max-width: 98vw;
    max-height: 98vh;
  }
  .modal-opcoes h4 {
    font-size: 1rem;
  }
  .modal-opcoes p, .modal-opcoes ul li {
    font-size: 0.85rem;
  }
  .modal-titulo {
    font-size: 1.3rem;
  }
  .button-medieval {
    width: 85%;
  }
}
@media (max-width: 480px) {
  .consulta-layout {
    padding: 0.5rem;
  }
  .parchment-card {
    max-width: 100%;
    padding: 0.8rem;
  }
  .medieval-disclaimer {
    font-size: 0.9rem;
    padding: 0.8rem 0.6rem;
  }
  .terms-section .terms-checkbox p {
    font-size: 0.9rem;
    line-height: 1.3;
  }
  .modal-conteudo {
    padding: 12px;
    font-size: 0.85rem;
  }
  .modal-opcoes h4 {
    font-size: 0.95rem;
  }
  .modal-opcoes p, .modal-opcoes ul li {
    font-size: 0.8rem;
  }
  .modal-titulo {
    font-size: 1.2rem;
  }
  .modal-botoes {
    flex-direction: column;
    gap: 0.5rem;
  }
  .modal-botoes .button-medieval {
    width: 100%;
  }
  .button-medieval {
    width: 90%;
  }
  .input-icon-group {
    gap: 0.3rem;
  }
  .svg-icon {
    width: 20px;
    height: 20px;
  }
